.chat {
    background-color: #363a3f;
    color: white;
    display: flex;
    flex: 0.70;
    flex-direction: column;
}

.chat_messages {
    flex: 1;
    overflow: scroll;
}

.chat_messages::-webkit-scrollbar {
    display: none;
}

.chat_messages {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.chat_input {
    align-items: center;
    background-color: #474b53;
    border-radius: 5px;
    border-top: 1px solid grey;
    color: lightgrey;
    display: flex;
    justify-content: center;
    margin: 20px;
    padding: 3px;
}

.chat_input > form > input {
    background: transparent;
    border: none;
    color: white;
    font-size: large;
    outline-width: 0;
    padding: 10px;
    width: 100%;
}

.chat_input > form {
    flex: 1;
}

.chat_input_button {
    display: none;
}

.chat_input_icons > .MuiSvgIcon-root {
    padding: 5px;
}